import React from 'react';

const NotFound = () =>
  <div>
    <img src={require("../images/404cat.jpeg")}/>
    <h3>404 page not found</h3>
    <p>We are sorry but the page you are looking for does not exist.</p>
  </div>

export default NotFound;



